import Vue from 'vue'
import header from './header/src/header' //
import content from './content/src/content' //
import footer from './footer/src/footer' //
import rightTools from './right-tools/src/right-tools' //
import progressBar from './progress-bar/src/progress-bar' //

const components = [
    content,
    footer,
    header,
    rightTools,
    progressBar,
]

let install = (Vue) => {
    components.map(component => {
        let componentName = "hc-" + component.name
        Vue.component(componentName, component);
    })
}
install(Vue)


