<template>
	<hc-content id="operateMain" :headerInformation="true" :headerMenu="true">
		<div class="main-container lrb pt10" v-if="main">
			<div>
				<el-form ref="queryForm" :model="queryForm" class="formEmbedTable mt20">
					<table>
						<tr>
							<td></td>
							<td>
								<el-form-item porp="inputComName">
									<!--@focus="cleanOther()"-->
									<el-input v-model="queryForm.inputComName"></el-input>
								</el-form-item>
							</td>
							<td></td>
							<td>
								<el-form-item porp="inputComType">
									<el-select v-model="queryForm.inputComType" clearable placeholder="">
										<el-option v-for="item in optionsType" :key="item.value" :label="item.label" :value="item.value">
										</el-option>
									</el-select>
								</el-form-item>
							</td>
							<td></td>
							<td>
								<el-form-item porp="inputComStatus">
									<el-select v-model="queryForm.inputComStatus" clearable placeholder="">
										<el-option v-for="item in optionsStatus" :key="item.value" :label="item.label" :value="item.value">
										</el-option>
									</el-select>
								</el-form-item>
							</td>
							<td class="td_01">
								<el-button type="primary" @click="queryList()"></el-button>
								<el-button @click="resetForm()"></el-button>
							</td>
						</tr>
					</table>
					<div class="fourForm tableForm mt20 ">
						<!--tableData-->
						<div class="div_test">
							<el-button type="primary" @click="add()"></el-button>
							<el-button type="danger" @click="deletes()"></el-button>
							<el-button type="primary" @click="shelves()"></el-button>
							<el-button type="primary" @click="theShelves()"></el-button>
						</div>
						<div>
						<happy-scroll color="rgba(0,0,0,0.5)" size="5" resize>
							<el-table :data="querysList">
								<el-table-column label="" width="100" align="center" type="selection">
									<!--<template slot-scope="scope">
										<el-radio-group v-model="selectedCst">
											<el-radio class="radio ml10" :label="scope.row" @change="handleSelectChange(scope.row)">&nbsp;</el-radio>
										</el-radio-group>
									</template>-->
								</el-table-column>
								<el-table-column prop="com_NAME" label="" align="center">
									<template slot-scope="scope">
										<el-button type="text" @click="openDetail(scope.row.com_ID)" class="cBlue"><span>{{scope.row.com_NAME}}</span></el-button>
									</template>
								</el-table-column>
								<el-table-column prop="com_TYPE" label="" align="center"></el-table-column>
								<el-table-column prop="com_VERSION" label="" align="center"></el-table-column>
								<el-table-column prop="com_STATUS" label="" align="center">
									<template slot-scope="scope">
										{{$tools.dict.getStt(scope.row.com_STATUS)}}
									</template>
								</el-table-column>
								<el-table-column label="" align="center">
									<template slot-scope="scope">
										<el-button @click="update()"></el-button>
										<el-button type="primary" @click="openDetail(scope.row.com_ID)"></el-button>
									</template>
								</el-table-column>
							</el-table>
							</happy-scroll>
						</div>
					</div>
				</el-form>
				<div class="pagination tar mt20">
					<el-pagination @current-change="handleCurrentChange" :current-page="Number(queryForm.currentPage)" :page-size="Number(queryForm.turnPageShowNum)" layout="total, prev, pager, next, jumper" :total="Number(turnPageTotalNum)">
					</el-pagination>
				</div>
			</div>
		</div>
	</hc-content>
</template>
<script>
	import axios from 'axios'
	import addComponents from './components/addComponents'
	import updComponents from './components/updComponents'
	import detComponents from './components/detComponents'
	export default {
		name: 'index',
		components: {
			addComponents,
			updComponents,
			detComponents
		},
		data() {
			return {
				main: true,
				isEdit: false,
				queryForm: {
					inputComName: '',
					inputComType: '',
					inputComStatus: '',
					currentPage: '1',
					turnPageShowNum: '10'
				},
				turnPageTotalNum: '',
				selectRadio: {},
				selectedCst: {},
				querysList: [],
				operInfoDetail: {},
				optionsType: [{
						value: '1',
						label: ''
					},
					{
						value: '2',
						label: ''
					}
				],
				optionsStatus: [{
						value: '1',
						label: ''
					},
					{
						value: '2',
						label: ''
					}
				],
				value4: '',
				value5: '',
			}
		},
		methods: {
			queryList() {
				let _this = this
				_this.selectRadio = {}
				axios.post('/api' + '/consumer/queryList.do', _this.queryForm).then(data => {
					if(data.data.code == 200) {
						_this.querysList = data.data.queryList
						console.log("querysList", _this.querysList)
						_this.turnPageTotalNum = data.data.turnPageTotalNum
                        console.log("turnPageTotalNum:",_this.turnPageTotalNum, "currentPage:",_this.currentPage)
					} else {
						_this.turnPageTotalNum = data.data.turnPageTotalNum
						_this.querysList = []
						_this.$alert(data.data.errorMsg, "", {
							confirmButtonText: '',
							type: 'warning',
							callback: action => {}
						})
					}
				})
			},
			handleCurrentChange(val) { //
                this.queryForm.currentPage = val;
                this.queryOperate()
            },
			openDetail(val) {
                //
                for (let i = 0; i < this.querysList.length; i++) {
                    if (this.querysList[i].com_id == val) {
                        this.operInfoDetail = this.querysList[i];
						console.log(this.operInfoDetail )
                        break;
                    }
                }
                this.main = false;
                this.isDetail = true;
            },
		},
		mounted() {
			//let _this = this
			//_this.queryList()
		}
	}
</script>
<style scoped>
	.td_01 {
		text-align: left;
	}
	
	.div_test {
		text-align: left;
		margin-bottom: 5px;
	}
</style>


