<template>
	<div class="main">
		<slot></slot>
	</div>
</template>

<script>
	import { mapActions } from 'vuex'

	export default {
		name: 'content',
		componentName: 'content',
		data() {
			return {};
		},
		props: {
			headerInformation: {
				type: Boolean
			},
			headerMenu: {
				type: Boolean
			},
			footerHasMarginTop: {
				type: Boolean,
				default: true
			},
		},
		methods: {
			...mapActions([
				'setHeaderInformation',
				'setHeaderMenu',
				'setFooterHasMarginTop'
			]),
			cancelFormSubmit() {
				let elFormLen = $('.el-form').length // el-form
				// console.log("form:" + elFormLen)
				for(let i = 0; i < elFormLen; i++) {
					let obj = $('.el-form').eq(i) //el-form
					obj[0].onsubmit = function(e) {
						//  el-formsubmit, enter
						return false
					}
				}
			}
		},
		created: function() {
			let _this = this
			_this.setHeaderInformation(_this.headerInformation)
			_this.setHeaderMenu(_this.headerMenu)
			_this.setFooterHasMarginTop(_this.footerHasMarginTop)
		},
		mounted() {
			// console.log('form:',$('.el-form').length,$('.el-form'))
			this.cancelFormSubmit()
		},
		updated() {
			//  form
			// console.log("form:" + $('.el-form').length)
			this.cancelFormSubmit()
		}
	}
</script>

<style>

</style>


